    --space-xxxxl: 4rem;   // 64px\n\n    // Application colors\n    --color-primary:          #e04b5a;\n    --color-primary-rotate:   #d6d0b8;\n    --color-primary-bg:       hsl(0, 85%, 96%);\n    --color-secondary:        #c46352;\n    --color-secondary-rotate: #8fb1e9;\n    --color-tertiary:         hsl(49, 89%, 64%);\n    --color-glint:            rgb(163, 209, 255);\n\n    // Neutral colors\n    --color-white:      hsl(0, 0%, 100%);\n    --color-background: hsl(30, 50%, 98%);\n    --color-light:      hsl(0, 6%, 93%);\n    --color-dark:       var(--color-text-header);\n\n    // Border radius\n    --border-radius: 0.375rem;\n\n    // Border\n    --border: solid 2px var(--color-light);\n    --border-dark: solid 2px var(--color-primary);\n\n    // Shadows\n    --shadow-large:  2px 4px 10px hsl(0 0% 0% / 0.1);\n    --shadow-small:  1px 3px 6px hsl(0 0% 0% / 0.1);\n}\n","*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n}\n\nhtml {\n  overflow-y: scroll;\n  height: 100%;\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  min-height: 100%;\n\n  background: url('../images/background.jpg') no-repeat center center fixed;\n  background-size: cover; \n  \n  // background-color: var(--color-background);\n  color: var(--color-text-body);\n  line-height: var(--line-height-body);\n  font-family: var(--font-family-sans);\n}\n\nimg,\npicture,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  color: var(--color-text-header);\n  line-height: var(--line-height-headers);\n}\n\nh1 {\n  font-size: var(--font-size-xxxl);\n}\n\nh2 {\n  font-size: var(--font-size-xxl);\n}\n\nh3 {\n  font-size: var(--font-size-xl);\n}\n\nh4 {\n  font-size: var(--font-size-l);\n}\n\na {\n  color: var(--color-primary);\n  text-decoration: none;\n  transition: color 200ms;\n\n  &:hover,\n  &:focus,\n  &:active {\n    color: var(--color-primary-rotate);\n  }\n}\n\n",".container {\n    // border: var(--border-dark);\n\n    position: relative;\n    display: grid;\n    gap: var(--space-xxxl);\n    grid-template-columns: 1fr;\n    grid-template-rows: 0.2fr 1fr 0.1fr;\n    grid-template-areas:\n      \"header\"\n      \"content\"\n      \"footer\";\n\n    width: 100%;\n    height: 100%;\n    // padding-right: var(--space-xs);\n    // padding-left: var(--space-xs);\n    margin-left: auto;\n    margin-right: auto;\n    // margin-top: var(--space-l);\n    \n    border-radius: var(--border-radius);\n    // box-shadow: var(--shadow-small);\n    background-color: var(--color-white);\n  \n    background-color: transparent;\n\n    @include media(tabletAndUp) {\n      // padding-right: var(--space-m);\n      // padding-left: var(--space-m);\n      max-width: 60rem;\n    }\n  }","@mixin media($query) {\n    @if $query == tabletAndUp {\n      @media (min-width: 50rem) { @content; }\n    }\n  }","header {\n    grid-area: header;\n    display: flex;\n\n    flex-direction: column;\n    justify-content: center;\n    // border: var(--border-dark);\n\n    h1 {\n      position: relative;\n      align-self: center;\n      color: var(--color-primary);\n      padding: var(--space-s) 0 var(--space-xl) 0;\n      background: linear-gradient(70deg, #e04b5a 50%, white 50%);\n      color: transparent; /* Hide the default text color */\n      background-clip: text; /* For other browsers */\n\n      .logo {\n        position: absolute;\n        left: 100px;\n        bottom: 55px;\n      }\n    }\n\n    .units {\n      justify-content: end;\n\n      display: flex;\n      gap: var(--space-xxxs);\n\n      .unit-group {\n        \n        background-color: var(--color-primary); /* Default background color */\n        color: var(--color-white); /* Default text color */\n        border: none; /* Remove border */\n        // border: solid 1px var(--color-secondary);\n        border-radius: var(--border-radius); /* Rounded corners */\n        padding: var(--space-s); /* Padding */\n        cursor: pointer; /* Pointer cursor on hover */\n        font-size: var(--font-size-m); /* Font size */\n        transition: background-color 0.3s, color 0.3s; /* Smooth transition for state changes */\n      } \n    \n      .unit-group.selected {\n          background-color: var(--color-white); /* Background color for selected state */\n          color: var(--color-dark); /* Text color for selected state */\n          border: 2px solid var(--color-accent);\n          pointer-events: none;\n          cursor: default; /* Pointer cursor on hover */ /* Optional: border to highlight the selected button */\n      }\n    }\n  }","footer {\n    grid-area: footer;\n    // margin: var(--space-s);\n    // border: var(--border-dark);\n    border-radius: var(--border-radius);\n    // height: 20px;\n}",".content {\n    grid-area: content;\n    // border: var(--border-dark);\n    border-radius: var(--border-radius);\n    // padding: 20px;\n    display: flex;\n    flex-direction: column;\n    // margin-top: -130px;\n}\n\n.overview {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: var(--space-m);\n\n    .overview-location {\n        padding: var(--space-xxs);\n        padding-left: var(--space-l);\n        padding-right: var(--space-l);\n        padding-bottom: 0px;\n        color: white;\n        font-size: var(--font-size-xxxl);\n        font-weight: bold;\n    }\n\n    .overview-body {\n        align-self: center;    \n        display: flex;\n        gap: var(--space-m);\n        align-items: center;\n        justify-content: center;\n        flex-wrap: wrap;\n    \n        .icon {\n            background-color: #d6d0b8d5;\n            border-radius: 5rem;\n            padding: var(--space-m);\n            width: 100px;\n            height: 100px;\n        }\n        .temp {\n            background-color: #d6d0b8d5;\n            // border: var(--border-dark);\n            border-radius: 5rem;\n            padding: var(--space-m);\n            font-size: var(--font-size-l);\n            font-weight: bold;\n            width: 100px;\n            height: 100px;\n            display: flex; /* Use Flexbox */\n            align-items: center; /* Vertically center */\n            justify-content: center; /* Horizontally center */\n        }\n        .time {\n            background-color: #d6d0b8d5;\n            // border: var(--border-dark);\n            border-radius: 5rem;\n            padding: var(--space-m);\n            font-size: var(--font-size-m);\n            font-weight: bold;\n            width: 100px;\n            height: 100px;\n            display: flex; /* Use Flexbox */\n            align-items: center; /* Vertically center */\n            justify-content: center; /* Horizontally center */\n        }\n    }    \n}\n\n.current-condition {\n    margin-top: 20px;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    flex-wrap: wrap;\n    gap: var(--space-m);\n}","// Card components\n.card {\n    display: flex;\n    flex-direction: column;\n    background-color: #d6d0b8d5;\n    padding: var(--space-m);\n    justify-content: center;\n    align-items: center;\n    height: 150px;\n    width: 150px;\n    border-radius: calc(var(--border-radius) * 2);\n\n    position: relative;\n\n    .card-title {\n        font-size: var(--font-size-s);\n        font-weight: 700;\n        position: absolute; // Use absolute positioning\n        top: var(--space-s); // Adjust the top offset if needed\n        left: var(--space-s); // Adjust the left offset if needed\n    }\n\n    .card-body {\n        text-align: center;\n        font-size: var(--font-size-xl);\n        font-weight: 700;\n    }\n}\n",".color {\n    display: inline-block;\n    padding: var(--space-xs);\n    margin: var(--space-xs);\n    margin-left: 0;\n    min-width: 250px;\n    height: var(--space-xxxxl);\n    box-sizing: border-box; \n    border: var(--border-dark);\n    box-shadow: var(--shadow-large);\n    border-radius: var(--border-radius);\n    \n    &::before {\n        content: attr(data-label); /* Display the label (e.g., primary) */\n        border-radius: inherit; /* Inherit parent's border radius */\n        display: flex;\n        align-items: left;\n        justify-content: left;\n        font-weight: bold;\n    }\n    // Specific color value styles\n    &::after {\n        content: attr(data-value); /* Display the color value (e.g., hsl(350, 67%, 50%)) */\n        display: flex;\n        align-items: center;\n        justify-content: flex-end; /* Align text to the right */\n        font-weight: bold;\n    }\n}\n\n",".btn {\n    // All the previous code\n    border: none;\n    height: var(--space-xxl);\n    cursor: pointer;\n\n    &--primary {\n      border-radius: var(--border-radius);\n      border: none;\n      color: var(--color-white);\n      background-color: var(--color-primary);\n      // background-image: linear-gradient(to right, var(--color-primary), var(--color-primary-rotate));\n      padding: var(--space-xs);\n  \n      &:hover,\n      &:focus,\n      &:focus-within,\n      &:active {\n        color: var(--color-white);\n        filter: saturate(1.4) brightness(115%);\n      }\n\n      &.round {\n        padding: var(--space-m);\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        border: none;\n        border-radius: 20%; // Makes the button circular\n        // padding: 10px; // Adjust padding to fit the round shape\n        // text-align: center; // Center text horizontally\n        width: 30px; // Adjust width as needed\n        height: 30px; // Ensure height matches width for a circle\n        font-size: var(--font-size-xl);\n      }\n    }\n  \n    &--secondary {\n      color: var(--color-white);\n      background-image: linear-gradient(to right, var(--color-secondary), var(--color-secondary-rotate));\n  \n      &:hover,\n      &:focus,\n      &:focus-within,\n      &:active {\n        color: var(--color-white);\n        filter: saturate(1.2) brightness(110%);\n      }\n\n      &.round {\n        border: none;\n        border-radius: 50%; // Makes the button circular\n        // padding: 10px; // Adjust padding to fit the round shape\n        // text-align: center; // Center text horizontally\n        width: 30px; // Adjust width as needed\n        height: 30px; // Ensure height matches width for a circle\n        font-size: var(--font-size-xl);\n      }\n    }\n  \n    &--light {\n      color: var(--color-dark);\n      background-color: var(--color-light);\n  \n      &:hover,\n      &:focus,\n      &:focus-within,\n      &:active {\n        color: var(--color-dark);\n        filter: brightness(92%);\n      }\n\n      &.round {\n        border-radius: 50%; // Makes the button circular\n        // padding: 10px; // Adjust padding to fit the round shape\n        // text-align: center; // Center text horizontally\n        width: 30px; // Adjust width as needed\n        height: 30px; // Ensure height matches width for a circle\n        font-size: var(--font-size-xl);\n      }\n    }\n  \n    &--dark {\n      color: var(--color-white);\n      border-color: var(--color-dark);\n      background-color: var(--color-dark);\n  \n      &:hover,\n      &:focus,\n      &:focus-within,\n      &:active {\n        color: var(--color-white);\n      }\n      &.round {\n        border-radius: 50%; // Makes the button circular\n        // padding: 10px; // Adjust padding to fit the round shape\n        // text-align: center; // Center text horizontally\n        width: 30px; // Adjust width as needed\n        height: 30px; // Ensure height matches width for a circle\n        font-size: var(--font-size-xl);\n      }\n    }\n\n    &--round {\n      appearance: none; /* Remove default styling */\n      -webkit-appearance: none; /* Remove default styling for WebKit browsers */\n      width: 15px; /* Width of the checkbox */\n      height: 15px; /* Height of the checkbox */\n      border-radius: 50%; /* Fully rounded */\n      outline: none; /* Remove outline */\n      cursor: pointer; /* Pointer cursor on hover */\n      position: relative; /* Positioning for custom checkmark */\n      background-color: #fff; /* Background color */\n      display: flex;\n      justify-content: center;\n      align-items: center;\n  \n      /* Default state (unchecked) */\n      &:before {\n        content: ''; /* Default circle */\n        border: var(--border-muted); /* Border for the circle */\n        border-radius: 50%; /* Fully rounded */\n        width: 15px; /* Width of the circle */\n        height: 15px; /* Height of the circle */\n      }\n  \n      /* Checked state */\n      &:checked::before {\n        content: ''; /* Remove the circle */\n        border: none; /* Ensure no border is applied */\n      }\n  \n      &:checked::after {\n        content: '✔'; /* Checkmark content */\n        font-size: 20px; /* Size of the checkmark */\n        color: var(--color-secondary); /* Checkmark color */\n      }\n    }\n  }","form {\n  // border: var(--border-dark);\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  gap: var(--space-s);\n  width: 100%;\n  flex-wrap: wrap; // Allows form elements to wrap to the next line if needed\n  \n\n  label {\n    // border: var(--border-dark);\n    display: flex;\n    flex-direction: column;\n    font-size: var(--font-size-xs);\n  }\n  \n  input {\n    width: 300px;\n    letter-spacing: 2px;\n    outline: none;\n    color: var(--color-light);\n    line-height: var(--space-xxl);\n    font-size: var(--font-size-l);\n    padding-left: var(--space-s);\n    border: 0;\n    border-bottom: solid 5px var(--color-primary);\n    background-color: transparent;\n  }\n\n  input::placeholder {\n    color: #ffffff; /* Sets the placeholder text color to white */\n    background-color: transparent;\n    opacity: 1; /* Ensures the placeholder text is fully opaque */\n  }\n  \n  .search-info {\n    padding: 4px;\n    color: #ffffff;; /* Set text color for error messages */\n    font-size: var(--font-size-xs); /* Adjust font size if needed */\n    margin-top: var(--space-xs); /* Space between input and error message */\n  }\n  /* Add styles for the suggestion box */\n#autocomplete-suggestions {\n  position: absolute; /* Position it relative to the form */\n  top: 100%; /* Position below the input field */\n  left: 0;\n  width: 100%; /* Same width as the input */\n  border: 1px solid var(--color-primary);\n  border-radius: var(--border-radius);\n  background-color: var(--color-background); /* Background color of suggestions */\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  z-index: 1000; /* Make sure it's on top */\n  max-height: 200px; /* Set a max height with scroll if needed */\n  overflow-y: auto; /* Scroll if there are many suggestions */\n}\n\n.suggestion {\n  padding: 10px;\n  cursor: pointer;\n  color: var(--color-light);\n  background-color: var(--color-background); /* Background for each suggestion */\n}\n\n.suggestion:hover {\n  background-color: var(--color-secondary); /* Background color on hover */\n  color: var(--color-dark); /* Text color on hover */\n}\n}\n\n","\nfieldset{\n  border: solid 2px var(--color-primary);\n  padding-bottom: var(--space-s);\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  background-color: var(--color-white);\n  box-shadow: var(--shadow-small);\n\n  letter-spacing: 2px;\n}\nlegend {\n  // padding-bottom: 25px;\n  margin-left: var(--space-m);\n  font-size: var(--font-size-xl);\n  color: var(--color-primary);\n} \n\n.fieldset-footer {\n  margin-top: var(--space-m);\n}","/* General loading styles */\n.loading {\n    // display: none;\n    position: relative;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n  \n  .loading::before {\n    content: '';\n    display: block;\n    width: 24px;\n    height: 24px;\n    border-radius: 50%;\n    border: 4px solid rgba(0, 0, 0, 0.1);\n    border-top: 4px solid #fff;\n    animation: spin 1s linear infinite;\n  }\n  \n  @keyframes spin {\n    0% { transform: rotate(0deg); }\n    100% { transform: rotate(360deg); }\n  }\n  "],"sourceRoot":""}]);
